@import '../../variables.css';

.ImageBlock__block {
  position: relative;
}

.ImageBlock__wrapper {
  position: relative;
}

.ImageBlock__block:not(:last-child) {
  margin-bottom: var(--padding-half);
}

.ImageBlock__img {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  border-radius: var(--border-radius);
  cursor: -webkit-zoom-in;
  cursor: zoom-in;
}

.ImageBlock__img--expanded {
  height: unset;
  width: unset;
  cursor: -webkit-zoom-out;
  cursor: zoom-out;
  top: 50%;
  left: calc(50% - 0.75rem);
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 1000;
  -webkit-box-shadow: 0 1px 20px 3px rgba(0, 0, 0, 0.4);
          box-shadow: 0 1px 20px 3px rgba(0, 0, 0, 0.4);
}

.ImageBlock__caption {
  margin-top: var(--padding-quarter);
}

.ImageBlock__placeholder {
  position: relative;
  border-radius: var(--border-radius);
  background: rgba(255, 255, 255, 0.3);
  overflow: hidden;
}

.ImageBlock__placeholder-image {
  position: absolute;
  top: 50%;
  left: 50%;
  text-align: center;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: var(--font-size-80);
  color: rgba(0,0,0,0.5);
}

.ImageBlock__placeholder-image::before {
  display: block;
  content: '';
  width: 8rem;
  height: 8rem;
  background: url('./img/loading.svg') center center/contain;
  background-repeat: no-repeat;
  margin-bottom: var(--padding-eighth);
  -webkit-transform: translateX(-0.15rem);
      -ms-transform: translateX(-0.15rem);
          transform: translateX(-0.15rem);
}



