@import '../variables.css';

.App {
  padding-top: var(--padding);
}

.App__nav {
  text-align: center;
  padding: 0 var(--padding) var(--padding-half);
  margin-bottom: var(--padding);
}

.App__nav-header {
  text-align: center;
  font-size: var(--font-size-115);
  font-weight: var(--font-weight-semi-bold);
  margin-bottom: var(--padding-quarter);
}

.App__nav-group-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding-bottom: var(--padding-quarter);
}

.App__nav-group {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  list-style: none;
  background: rgba(255, 255, 255, 0.9);
}

.App__nav-link {
  display: inline-block;
  padding: var(--padding-quarter);
  font-size: var(--font-size-115);
}

.App__nav-link:hover {
  color: var(--warm-blue);
}

.App__header {
  margin: 0 var(--padding) var(--padding);
  text-align: center;
  font-weight: var(--font-weight-light);
  font-size: var(--font-size-280);
}

.App__intro {
  max-width: var(--max-width);
  padding: 0 var(--padding);
  margin: 0 auto var(--padding);
}

.App__closing {
  max-width: var(--max-width);
  padding: var(--padding);
  margin: var(--padding) auto 0;
}

.App__summary {
  position: relative;
  max-width: var(--max-width);
  padding: var(--padding);
  margin: var(--padding-quarter) auto 0;
  text-align: center;
}

.App__summary::before {
  position: absolute;
  top: 0;
  left: 50%;
  width: 60%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  border-top: 1px solid var(--black-a20);
  content: '';
}

.App__summary-header {
  font-weight: var(--font-weight-semi-bold);
  font-size: var(--font-size-100);
  margin-bottom: var(--padding-half);
}

.App__summary-table {
  display: inline-block;
}

.App__footer {
  max-width: var(--max-width);
  margin: 0 auto;
}

.App__footer-image {
  display: block;
  width: 100%;
}



