@import '../variables.css';

.Markdown:not(:last-child) {
  margin-bottom: var(--padding-half);
}

.Markdown--condensed:not(:last-child) {
  margin-bottom: var(--padding-quarter);
}

.Markdown a {
  color: inherit;
}

.Markdown p:not(:last-child) {
  margin-bottom: var(--padding-half);
}

.Markdown--condensed p:not(:last-child) {
  margin-bottom: var(--padding-quarter);
}

.Markdown ul,
.Markdown ol {
  margin-left: var(--padding-half);
}

.Markdown li {
  margin-bottom: var(--padding-eighth);
}

.Markdown ul:not(:last-child),
.Markdown ol:not(:last-child) {
  margin-bottom: var(--padding-half);
}

.Markdown--condensed ul:not(:last-child),
.Markdown--condensed ol:not(:last-child) {
  margin-bottom: var(--padding-quarter);
}

.Markdown blockquote {
  background: rgba(255, 255, 255, 0.1);
  border-left: 0.3rem solid rgba(255, 255, 255, 0.5);
  border-radius: var(--border-radius);
  padding: var(--padding-half);
  margin-left: var(--padding-quarter);
}

.Markdown blockquote:not(:last-child) {
  margin-bottom: var(--padding-half);
}

.Markdown--condensed blockquote:not(:last-child) {
  margin-bottom: var(--padding-quarter);
}




