@import '../../variables.css';

:root {
  --timeline-icon-width: var(--padding-half);
  --timeline-border-width: 0.3rem;
}

.Timeline {
  list-style: none;
}

.Timeline__entry {
  position: relative;
  display: -ms-flexbox;
  display: flex;
}

.Timeline__entry:not(:last-child) .Timeline__entry-icon::before {
  position: absolute;
  top: calc(var(--timeline-icon-width) - 1px);
  width: var(--timeline-border-width);
  background: rgba(255, 255, 255, 0.5);
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  content: '';
  bottom: -1px;
}

.Timeline__entry-icon {
  position: relative;

  width: var(--timeline-icon-width);
}

.Timeline__entry-icon::after {
  position: absolute;
  content: '';
  width: 100%;
  top: 0;
  left: 0;
  padding-top: 100%;
  background: center center/100% url('./img/moment.svg');
}

.Timeline__entry-icon--love::after {
  background-image: url('./img/moment-love.svg');
}

.Timeline__entry-icon--summary::after {
  background-image: url('./img/moment-checkmark.svg');
}

.Timeline__entry-icon--funding::after {
  background-image: url('./img/moment-funding.svg');
}

.Timeline__entry-icon--sad::after {
  background-image: url('./img/moment-sad.svg');
}

.Timeline__entry-icon--emoji::after,
.Timeline__entry-icon--aashayCommits::after,
.Timeline__entry-icon--happy::after {
  background-image: url('./img/moment-happy.svg');
}

.Timeline__entry-icon--locked::after {
  background-image: url('./img/moment-locked.svg');
}

.Timeline__entry-icon--quote::after {
  background-image: url('./img/moment-quote.svg');
}

.TimelineEntry__entry-content {
  position: relative;
  -ms-flex: 1 1;
      flex: 1 1;
  padding-left: var(--padding-half);
  padding-bottom: var(--padding);
}

.TimelineEntry__entry-date {
  position: absolute;
  width: 100%;
  bottom: 100%;
  padding-bottom: 0;
  color: rgba(255, 255, 255, 0.5);
  text-decoration: none;
}

.TimelineEntry__entry-date::after {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  content: '';
  display: inline-block;
  margin-left: var(--padding-eighth);
  background: url('./img/link.svg') center center/contain no-repeat;
  width: 1.3em;
  height: 1.3em;
  opacity: 0;
}

.TimelineEntry__entry-date:hover.TimelineEntry__entry-date::after {
  opacity: 1;
}

.TimelineEntry__header {
  margin: 0;
  padding: 0;
  font-weight: var(--font-weight-semi-bold);
  font-size: var(--font-size-115);
  line-height: 1.2;
}



