@import '../../variables.css';

.EmojiEntry {
  padding-top: var(--padding-quarter);
}

.EmojiEntry__single-description {
  margin: var(--padding-half) 0;
}

.EmojiEntry__table {
  width: 100%;
  border-collapse: collapse;
}

.EmojiEntry__table-row {
  padding: var(--padding-half);
}

.EmojiEntry__table-row:not(:last-child) {
  border-bottom: 1px solid var(--black-a05);
}

.EmojiEntry__table-cell:first-child {
  vertical-align: top;
  padding: var(--padding-half);
  width: 40%;
}

.EmojiEntry__table--single .EmojiEntry__table-cell:first-child {
  padding: var(--padding-quarter);
  width: calc(32px + var(--padding-half));
}

.EmojiEntry__table-cell:not(:first-child) {
  padding: var(--padding-half);
  padding-left: 0;
  vertical-align: top;
}

.EmojiEntry__table--single .EmojiEntry__table-cell:not(:first-child) {
  padding: var(--padding-quarter);
  padding-left: 0;
  vertical-align: middle;
}

.EmojiEntry__emoji-message {
  font-family: monospace;
  font-size: var(--font-size-80);
  word-break: break-all;
  white-space: pre-wrap;
  padding: var(--padding-quarter);
  background: var(--black-a05);
  border-radius: var(--border-radius);
}

.EmojiEntry__emoji-message:not(:last-child) {
  margin-bottom: var(--padding-quarter);
}

.EmojiEntry__block-row {
  display: -ms-flexbox;
  display: flex;
}

.EmojiEntry__block-cell {
  font-size: 0;
  -ms-flex: 1 1;
      flex: 1 1;
  background: center center/contain no-repeat;
}

.EmojiEntry__table--single .EmojiEntry__block-cell {
  max-width: 64px;
}

.EmojiEntry__block:hover .EmojiEntry__block-cell:not(:last-child) {
  margin-right: var(--padding-eighth);
}

.EmojiEntry__block:hover .EmojiEntry__block-row:not(:last-child) {
  margin-bottom: var(--padding-eighth);
}

.EmojiEntry__block-cell::after {
  display: block;
  padding-top: 100%;
  width: 100%;
  content: '';
  font-size: 0;
}




