@import '../../variables.css';

.VideoBlock__player {
  position: relative;
  width: 100%;
  border-radius: var(--border-radius);
  background: rgba(255, 255, 255, 0.5);
}

.VideoBlock__video {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  border-radius: var(--border-radius);
}

.VideoBlock__caption {
  padding-top: var(--padding-quarter);
}



