@import '../../variables.css';

.EmailThreadEntry {
  padding-top: var(--padding-quarter);
}

.EmailThreadEntry__thread {
  list-style: none;
  padding: var(--padding-half) 0;
}

.EmailThreadEntry__subject {
  font-size: var(--font-size-115);
  font-weight: var(--font-weight-semi-bold);
  padding: var(--padding-quarter) var(--padding-half);
  border-bottom: 1px solid var(--black-a20);
}

.EmailThreadEntry__email {
  padding-left: var(--padding-half);
  padding-right: var(--padding-half);
}

.EmailThreadEntry__email:not(:last-child) {
  margin-bottom: var(--padding-half);
}

.EmailThreadEntry__email:not(:first-child) {
  border-top: 1px solid var(--black-a20);
  padding-top: var(--padding-half);
}

.EmailThreadEntry__email-head {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding-bottom: var(--padding-half);
}

.EmailThreadEntry__email-avatar {
  height: 2rem;
  width: 2rem;
  border-radius: 1rem;
  margin-right: var(--padding-quarter);
  background: no-repeat center center/contain;
}

.EmailThreadEntry__email-avatar--me {
  background-image: url('./img/me.jpg');
}

.EmailThreadEntry__email-avatar--matt {
  background-image: url('./img/matt.jpg');
}

.EmailThreadEntry__email-name {
  -ms-flex: 1 1;
      flex: 1 1;
  font-weight: var(--font-weight-semi-bold);
}

.EmailThreadEntry__email-date {
  font-weight: var(--font-weight-normal);
  color: var(--black-a20);
}

.EmailThreadEntry__email-body {
  font-weight: var(--font-weight-normal);
}

.EmailThreadEntry__description {
  margin: var(--padding-half) 0 0;
}



