@import '../variables.css';

.Section {
  position: relative;
  color: white;
  padding: var(--padding) var(--padding) var(--padding) var(--padding-half);
}

.Section {
  margin: 100px 0;
}

.Section:nth-child(1) { background: var(--year-1-color); }
.Section:nth-child(2) { background: var(--year-2-color); }
.Section:nth-child(3) { background: var(--year-3-color); }
.Section:nth-child(4) { background: var(--year-4-color); }
.Section:nth-child(5) { background: var(--year-5-color); }
.Section:nth-child(6) { background: var(--year-6-color); }
.Section:nth-child(7) { background: var(--year-7-color); }
.Section:nth-child(8) { background: var(--year-8-color); }

.Section::before {
  content: '';
  position: absolute;
  top: -55px;
  left: 0;
  width: 100%;
  bottom: -55px;
  z-index: -1;
  -webkit-transform: skewY(3deg);
      -ms-transform: skewY(3deg);
          transform: skewY(3deg);
}

.Section:nth-child(1)::before { background: var(--year-1-color); }
.Section:nth-child(2)::before { background: var(--year-2-color); }
.Section:nth-child(3)::before { background: var(--year-3-color); }
.Section:nth-child(4)::before { background: var(--year-4-color); }
.Section:nth-child(5)::before { background: var(--year-5-color); }
.Section:nth-child(6)::before { background: var(--year-6-color); }
.Section:nth-child(7)::before { background: var(--year-7-color); }
.Section:nth-child(8)::before { background: var(--year-8-color); }

.Section__header  {
  font-size: var(--font-size-350);
  text-align: center;
  font-weight: var(--font-weight-light);
  margin-bottom: var(--padding-quarter);
}

.Section__preface {
  text-align: center;
  max-width: var(--max-width);
  margin: 0 auto var(--padding);
  font-size: var(--font-size-115);
  padding-bottom: var(--padding-half);
}

.Section__preface-text {
  display: inline-block;
  text-align: left;
}

.Section__timeline {
  max-width: var(--max-width);
  margin: 0 auto;
}

.Section__summary {
  max-width: calc(var(--max-width) / 2);
  margin: 0 auto var(--padding);
}

.Section__navigation {
  max-width: var(--max-width);
  margin: 0 auto;
  text-align: center;
}

.Section__navigation-link {
  color: inherit;
  text-decoration: none;
}


