:root {
  --padding: 3rem;
  --padding-half: 1.5rem;
  --padding-quarter: 0.75rem;
  --padding-eighth: 0.375rem;

  --font-color-default: #4a4a4a;

  --font-weight-light: 300;
  --font-weight-normal: 400;
  --font-weight-semi-bold: 600;

  --font-size-80: 0.8rem;
  --font-size-100: 1rem;
  --font-size-115: 1.15rem;
  --font-size-280: 2.8rem;
  --font-size-350: 3.5rem;

  --border-radius: 0.2rem;

  --max-width: 40rem;

  --black-a50: rgba(0, 0, 0, 0.5);
  --black-a20: rgba(0, 0, 0, 0.2);
  --black-a05: rgba(0, 0, 0, 0.05);

  --warm-blue: #007db6;
  --cool-blue: #0091b2;
  --teal: #009c9e;
  --viridian: #00a77e;
  --cool-green: #22b24c;
  --leaf-green: #60b031;
  --yellow-green: #a5b62e;

  --year-1-color: #007db6;
  --year-2-color: #0091b2;
  --year-3-color: #009c9e;
  --year-4-color: #00a388;
  --year-5-color: #0baa6d;
  --year-6-color: #22b24c;
  --year-7-color: #60b031;
  --year-8-color: #a5b62e;

  --red: #C60000;
}


