@import '../../variables.css';

.SummaryEntry__header {
  margin: 0;
  padding: 0;
  font-weight: var(--font-weight-semi-bold);
  font-size: var(--font-size-115);
  margin-bottom: var(--padding-quarter);
  line-height: 1.2;
}

.SummaryEntry__table-header {
  font-size: var(--font-size-115);
  font-weight: var(--font-weight-semi-bold);
  padding: var(--padding-quarter) 0 var(--padding-eighth);
}

.SummaryEntry__summary {
  margin: var(--padding-quarter) 0;
}

.SummaryEntry__table {
  font-size: var(--font-size-115);
  margin-left: var(--padding-quarter);
}



