@import '../../variables.css';

.AashayCommitsEntry {
  padding-top: var(--padding-quarter);
}

.AashayCommitsEntry__description {
  margin-bottom: var(--padding-half);
  white-space: pre-wrap;
}

.AashayCommitsEntry__commits {
  border-collapse: collapse;
  width: 100%;
}

.AashayCommitsEntry__commit-cell {
  padding: var(--padding-quarter) 0 var(--padding-quarter) var(--padding-quarter);
  border-bottom: 1px solid var(--black-a05);
}

.AashayCommitsEntry__commit-cell:last-child {
  padding-right: var(--padding-quarter);
}

.AashayCommitsEntry__commit-cell--message {
  font-family: courier, monospace;
  font-size: var(--font-size-80);
  vertical-align: top;
}

.AashayCommitsEntry__commit-cell--avatar::before {
  display: block;
  height: 1.18rem;
  width: 1.18rem;
  background: url('./img/aashay.png') center center/cover;
  content: '';
}

.AashayCommitsEntry__commit-cell--counts {
  vertical-align: top;
  text-align: right;
  font-weight: var(--font-weight-semi-bold);
}

.AashayCommitsEntry__count:not(:last-child) {
  padding-right: var(--padding-quarter);
}

.AashayCommitsEntry__count--addition {
  color: var(--cool-green);
}

.AashayCommitsEntry__count--deletions {
  color: var(--red);
}



