@import '../variables.css';

.StatsTable__table {
  font-size: var(--font-size-115);
  margin-left: var(--padding-quarter);
}

.StatsTable__row--space-after .StatsTable__cell {
  padding-bottom: var(--padding-half);
}

.StatsTable__cell:first-child {
  text-align: left;
}

.StatsTable__cell:last-child {
  text-align: right;
  font-weight: var(--font-weight-semi-bold);
  padding-left: var(--padding);
}


